.home {
  &-body {
    width: 100%;
    display: flex;
    background-color: #000000;
    flex-direction: column;
    align-items: center;
    height: 100%;
    padding-bottom: 100px;
  }

  &__title {
    width: 100%;
    text-align: center;

    h3 {
      font: {
        family: Ubuntu;
        size: 30px;
        weight: bold;
      }
      color: whitesmoke;
    }
  }

  &__content {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;

    &--container {
      display: flex;
      flex-direction: row;
      background-color: rgba(40, 95, 183, 0.27);
      align-items: center;
      width: 100%;
      justify-content: space-between;
      font: {
        family: Ubuntu;
      }
    }

    &--title {
      font: {
        size: 25px;
        weight: bold;
      }

      &:first-letter {
        text-transform: uppercase;
      }

      color: #5097d4;
      width: 100%;
      margin-left: 10px;
    }

    &--content {
      width: 100%;
      font: {
        size: 15px;
      }
      letter-spacing: 0.2em;
      word-wrap: break-word;
      max-width: 70%;
      color: #bebebe;
    }
  }
}