.users {
  &-body {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background-color: black;
  }

  &__control-panel {
    &--container {
      display: flex;
      width: 60%;
      margin: 10px auto;
      align-items: center;
      justify-content: center;
    }

    &--btn {
      padding: 10px 40px;
      border-radius: 10px;
      border: 2px solid #68a3ff;
      background-color: #2035b7;
      color: #bebebe;
      margin: auto 40px;
      font: {
        family: Ubuntu;
        size: 20px;
      }
    }
  }

  &__list {
    &-header {
      display: flex;
      flex-direction: row;
      width: 80%;
      padding: 10px 40px;
      margin: 10px auto;
      align-items: center;
      justify-content: space-around;
      border: 1px solid #285fb7;

      &--title {
        font: {
          family: Ubuntu;
          size: 25px;
          weight: bold;
        }
        color: whitesmoke;
        width: 15%;
        text-align: center;

        &:first-letter {
          text-transform: uppercase;
        }
      }
    }

    &--item {
      &-container {
        display: flex;
        flex-direction: row;
        width: 80%;
        padding: 10px 40px;
        margin: 2px auto;
        align-items: center;
        justify-content: space-around;
        border: 1px solid rgba(40, 95, 183, 0.55);
        text-decoration: none;

        &__active {
          background-color: red;
        }
      }

      &-container:hover {
        background-color: rgba(40, 112, 203, 0.12);
      }

      &-container:active {
        background-color: rgba(40, 104, 194, 0.38);
      }

      &-content {
        font: {
          family: Ubuntu;
          size: 20px;
        }
        color: #4958a8;
        width: 15%;
        text-align: center;
      }
    }

  }
}