.nav-bar {
  display: flex;
  width: 100%;
  background-color: #000000;
  justify-content: center;
  border-top: 2px solid whitesmoke;
  border-bottom: 2px solid whitesmoke;
  &__container {
    display: flex;
    width: 60%;
    margin: 20px 0;
    justify-content: space-around;
    flex-wrap: wrap;

    .nav__link {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 20px 0;
      margin: 10px 0;
      background-color: #282828;
      border: 2px white solid;
      border-radius: 30px;
      min-width: 200px;

        text-decoration: none;
        color: white;
        font:{
          family: Ubuntu;
          size:20px;
          weight: bold;
      }

      &__active {
        border: 2px #939393 solid;
        background-color: #285fb7;
      }
    }
  }
}
