.authorization {
  &-body {
    width: 100%;
    display: flex;
    background-color: #000000;
    flex-direction: column;
    align-items: center;
    height: 100%;
    padding-bottom: 100px;
  }

  &__title {
    width: 100%;
    text-align: center;

    h3 {
      font: {
        family: Ubuntu;
        size: 30px;
        weight: bold;
      }
      color: whitesmoke;
    }
  }

  &__content {
    width: 50%;
    display: flex;
    align-items: center;
    background-color: rgba(40, 95, 183, 0.27);
    border: 1px solid whitesmoke;

    &--status {
      display: flex;
      flex-direction: column;
      width: 40%;
      height: 100%;
      align-items: center;
      justify-content: space-evenly;
      p{
        font:{
          family: Ubuntu;
          size: 20px;
          weight: bold;
        }
        color: whitesmoke;
        margin: auto 10px;
      }
    }

    &--form {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 60%;
      border-right: 1px solid whitesmoke;
      font: {
        family: Ubuntu;
      }
      *{
        outline: none;
      }

      .blockLogin {
        display: flex;
        flex-direction: row;
        align-items: flex-end;
        justify-content: space-between;
        width: 90%;
        margin: 30px auto 10px auto;

        .labelLogin {

          font: {
            family: Ubuntu;
            size: 30px;
            weight: bold;
          }
          color: whitesmoke;
        }

        .inputLogin {
          font: {
            family: Ubuntu;
            size: 20px;
          }
          padding: 5px 10px;
          border-radius: 10px;
          border: 1px solid white;
        }
      }

      .blockPassword {
        display: flex;
        flex-direction: row;
        align-items: flex-end;
        justify-content: space-between;
        width: 90%;
        margin: 10px auto;

        .labelPassword {
          font: {
            family: Ubuntu;
            size: 30px;
            weight: bold;
          }
          color: whitesmoke;
        }

        .inputPassword {
          font: {
            family: Ubuntu;
            size: 20px;
          }
          padding: 5px 10px;
          border-radius: 10px;
          border: 1px solid white;
        }
      }

      button[type='submit'] {
        padding: 10px 40px;
        font: {
          family: Ubuntu;
          size: 25px;
          weight: bold;
        }
        color: #285fb7;
        border-radius: 40px;
        border: 1px solid whitesmoke;
        margin: 20px auto;
      }
    }


  }
}